import Vue from 'vue';
import Router from 'vue-router';
import store from './store';

import Main from './views/Main.vue';
import Registration from './views/Registration.vue';
import Login from './views/Login.vue';
import Projects from './views/Projects.vue';
import Project from './views/Project.vue';
import Account from './views/Account.vue';
import AccountProjects from './views/AccountProjects.vue';
import AccountProjectsAdd from './views/AccountProjectsAdd.vue';

const ifNotAuthenticated = (to, from, next) => {
  if (!store.getters.isAuthenticated) {
    next();
    return;
  }
  next('/');
};

const ifAuthenticated = (to, from, next) => {
  if (store.getters.isAuthenticated) {
    next();
    return;
  }
  next('/login');
};


Vue.use(Router);

export default new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'main',
      component: Main
    },
    {
      path: '/registration',
      name: 'registration',
      component: Registration
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      beforeEnter: ifNotAuthenticated,
    },
    {
      path: '/projects',
      name: 'projects',
      component: Projects
    },
    {
      path: '/projects/:id',
      name: 'project',
      component: Project
    },
    {
      path: '/account',
      name: 'account',
      component: Account,
      beforeEnter: ifAuthenticated,
    },
    {
      path: '/account/projects',
      name: 'accountProjects',
      component: AccountProjects,
      beforeEnter: ifAuthenticated,
    },
    {
      path: '/account/add',
      name: 'accountProjectsAdd',
      component: AccountProjectsAdd,
      beforeEnter: ifAuthenticated,
    },
  ]
});
